
export const COINS = [1, 10, 50, 100, 500, 1000, 5000, 10000];

export const AVAILABLE_COINS = [10, 50, 100, 500, 1000];

export const createInitialInputs = () => {
  return COINS.map((val) => ({ value: val, count: 0 }));
}

export const isAvailable = (value) => (AVAILABLE_COINS.includes(value))

